import { Button } from 'antd'
import React, { useEffect, useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import six from '../../images/six.png'
import chatIcon from '../../images/chat.png'
import stepNavigationIcon from '../../images/six.png'
import { useDispatch } from 'react-redux'
import {notificationEnable, update_locationEnable } from '../../redux/actions/questions'
import { getLocationFromGeo } from '../../constant'
import BtnLoading from '../../component/BtnLoading'

const NotificationEnable = () => {
    let fcmToken = localStorage.getItem('fcm')
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const [lat, setLat] = useState(null);
    const [lng, setLng] = useState(null);
    const [status, setStatus] = useState(null);
    const [btnLoading,setBtnLoading] = useState(false)
    const [btnLoading2,setBtnLoading2] = useState(false)

    useEffect(()=>{
        if (!navigator.geolocation) {
            setStatus('Geolocation is not supported by your browser');
        } else {
            setStatus('Locating...');
            navigator.geolocation.getCurrentPosition((position) => {
                setStatus(null);
                setLat(position.coords.latitude);
                setLng(position.coords.longitude);

                // console.log(position.coords)
                let locationdata ={
                    lat:position.coords.latitude,
                    lng:position.coords.longitude,
                    userlat : localStorage.setItem("userlat",position.coords.latitude),
                    userlng : localStorage.setItem("userlng",position.coords.longitude)
                }
                const successCb=(res)=>{
                    console.log("succResss",res)
                //   navigate('/notification_enable')
                }
                const failureCb=(res)=>{
                    console.log("failcb",res)
                }
                dispatch(update_locationEnable(locationdata,successCb,failureCb))
            }, () => {
                setStatus('Unable to retrieve your location');

            });
        }
    },[])

    let payload ={
        notification_status:"",
        fcm_token:fcmToken
    }
    const wantToNotify = ()=>{
        payload.notification_status = 1
        setBtnLoading(true)
        const successCb=(res)=>{
            setBtnLoading(false)
            console.log("succb",res)
            navigate("/home")
        }
        const failureCb=(res)=>{
            setBtnLoading(false)
            console.log("failureRes",res)
        }
        console.log("allowPayload",payload)
        dispatch(notificationEnable(payload,successCb,failureCb))
    }
    const notNotify =()=>{
        setBtnLoading(true)
        setBtnLoading2(true)
        payload.notification_status = 0
        const successCb=(res)=>{
            setBtnLoading(false)
            setBtnLoading2(false)
            console.log("succb",res)
            navigate("/home")
        }
        const failureCb=(res)=>{
            setBtnLoading2(false)
            setBtnLoading(false)
            console.log("failureRes",res)
        }
       console.log('notPayload',payload)
        dispatch(notificationEnable(payload,successCb,failureCb))
    }
    return (
        <>
            <div className='stepsBackground'>  
                <div className='underDiv'>
                    <div className='container containeClassDiv'>
                        <div className='backAndLogo' style={{ width: "100%" }}>
                            <Link to={'/locationEnable'} style={{ marginLeft: '1rem' }}><img src="plugins/images/Group 17194.png" alt='' className='backIconHeight'/></Link>
                            <img style={{ maxHeight: '200px', width: '200px' }} src='plugins/images/Mask Group 19@2x.png' />
                            <p></p>
                        </div>
                        <div className='stepss'>
                            <img src={six} alt="img_err"  className='img-fluid' />
                            
                            <div style={{ marginTop: 40 }} className="">
                                <h5 style={{ color: 'white', textAlign: "center" }}>Enable Notification's</h5>
                                <p className='text-center text-white'>Get push-notification when you get the match <br/> or receive a message.You can turn this On/Off <br/> Later in Settings.</p>
                                <div className='mt-2 d-flex justify-content-center'>
                                    <img src={chatIcon} style={{ width: '140px', height: '140px' }} />
                                </div>
                            </div>
                            <div className='selectionGenderBox qsntLastBackImage' style={{ marginTop: 40 }}>
                                <Button disabled = {btnLoading?true:false} style={{padding:"6px 60px"}} onClick={wantToNotify} className='continueBtn qsnsLastBtn' >
                                
                                {
                                    btnLoading?(
                                      <BtnLoading/>
                                    ):(
            
                                     "I want to be Notified"
                                    )
                                  } 
                                </Button>
                            </div>
                            <Button disabled = {btnLoading2?true:false} type='text' onClick={notNotify} className='text-center notNow mb-4 mt-4' style={{ color: '#FA81F0',fontSize:'18px' }} >                          
                            {
                                btnLoading2?(
                                  <BtnLoading/>
                                ):(
                                 "Not Now"
                                )
                              } 
                            </Button>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default NotificationEnable