import React, { useEffect, useRef, useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import two from '../../images/two.png'
import Slider from "react-slick";
import DatingImg from "../../images/Dating.png"
import friendShipImg from "../../images/friendship.png"
import HookUpImg from "../../images/HookUp.png"
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { useDispatch, useSelector } from 'react-redux';
import { getConnectionType, UpdateConnectionType } from '../../redux/actions/questions';
import { fileUrl } from '../../constant';
import { toast } from 'react-toastify';
import { questions_select } from '../../toastMessages';
import { Button } from 'antd';
import Loader from '../../component/Loader';
import BtnLoading from '../../component/BtnLoading';



const TypeOfConnection = () => {
    const connectionDataRedux = useSelector((state) => state?.questions?.get_connection_typeData)
    const [slideImageId, setSlideImageId] = useState('')
    const [selectIdArr, setSelectidArr] = useState([])
    const [loading, setLoading] = useState(false)
    const [btnLoading,setBtnLoading] = useState(false)
    console.log("connectionDataRedux", connectionDataRedux)
    var settings = {
        dots: true,
        // infinite: true,
        speed: 300,
        slidesToShow: connectionDataRedux?.length > 3 ? 3 : 1,
        slidesToScroll: 1,
        // autoplay: true,
        centerMode: true,
        centerPadding: '50px',
        // focusOnSelect: true,
        arrows: true,
        useCSS: true,
        responsive: [{
            breakpoint: 1025,
            settings: {
                slidesToShow: 3,
            }
        }, {
            breakpoint: 600,
            settings: {
                slidesToShow: 2,
            }
        }, {
            breakpoint: 452,
            settings: {
                slidesToShow: 1,
            }
        }
        ]
    };


    const navigate = useNavigate()
    const dispatch = useDispatch()

    useEffect(() => {
        setLoading(true)
        const successCb = (res) => {
            console.log("succRes", res)
            setLoading(false)
        }
        const failureCallback = (res) => {
            setLoading(false)
            console.log("failcbRes", res)
        }
        dispatch(getConnectionType(successCb, failureCallback))
    }, [])

    const slider = useRef()

    const selectAndNext = (id) => {
        console.log("runnn")
        var elemntId = ''
        let arr = []
        slider.current.slickNext();
        console.log("id", id)
        setSlideImageId(id)
        arr.push(id)
        let newArr = [id, ...selectIdArr]


        connectionDataRedux?.forEach(element => {
            if (element?._id == id) {
                if (element?.borderColor) {

                    connectionDataRedux[connectionDataRedux.indexOf(element)].borderColor = ""
                } else {

                    connectionDataRedux[connectionDataRedux.indexOf(element)].borderColor = "2px solid #00DFDF"
                }
            }
        });

        // for unselect
        selectIdArr?.forEach(ele => {
            if (ele == id) {
                console.log("again")
                elemntId = ele
                let myArr = selectIdArr.filter((item, indx) => item != id)
                console.log("myArrr", myArr)
                setSelectidArr(myArr)
            }
        })


        if (elemntId == "") {
            setSelectidArr(newArr)
        }
    }

    console.log("selectArr", selectIdArr)

    const connectionSubmit = () => {
        // navigate('/interest')
        if (selectIdArr?.length > 0) {
            setBtnLoading(true)
            let connectionIds = selectIdArr.join(',')
            console.log("interestIds", connectionIds)
            localStorage.setItem("connectionId", connectionIds)
            const succCb = (res) => {
                console.log("succcbbb", res)
                navigate('/interest')
                setBtnLoading(false)
            }
            const failCb = (res) => {
                setBtnLoading(false)
                console.log("failll", res)
            }
            let payload = {
                connection_type: connectionIds
            }
            dispatch(UpdateConnectionType(payload, succCb, failCb))
        } else {
            toast.error(questions_select)
        }
    }
    return (
        <>
            <div className='stepsBackground'>
                <div className='underDiv'>

                    <div className='container'>
                        <div className='backAndLogo'>
                            <Link to={'/gender'} style={{ marginLeft: '1rem' }}><img src="plugins/images/Group 17194.png" alt='' className='backIconHeight' /></Link>
                            <img class="img-fluid" style={{ maxHeight: '200px', width: '200px' }} src='plugins/images/Mask Group 19@2x.png' />
                            <p></p>
                        </div>

                        <div className='stepss'>
                            <img src={two} alt="img_err" height="50px" className='img-fluid' />
                            <div style={{ marginTop: 40 }}>
                                <h5 style={{ color: 'white', textAlign: "center" }}>Types of Connection</h5>
                            </div>
                            {
                                loading ? (<Loader />) : (
                                    <>

                                        <div className='w-100 text-center  sliderMainDiv' >
                                            <Slider ref={slider} {...settings} {...settings} className='slider' >
                                                {connectionDataRedux && connectionDataRedux.map((item, indx) => {
                                                    console.log("item", item)
                                                    let imagUrl = `${fileUrl}${item?.images[0]?.name}`
                                                    return (
                                                        <>
                                                            <div style={{ textAlign: 'center', opacity: '0.6' }} onClick={() => selectAndNext(item?._id)} ><img style={{ opacity: '0.2', marginLeft: '15%', marginTop: '5%', border: item?.borderColor ? item?.borderColor : "", borderRadius: '12px', width: '250px' }} className="img w-75" src={imagUrl} /><p className=" text-white text-center pt-3">Dating</p></div>
                                                        </>
                                                    )
                                                })}


                                            </Slider>
                                        </div>
                                        <div className='selectionGenderBox qsntLastBackImage mb-4' style={{ height: 'auto !important' }}>
                                            <Button disabled = {btnLoading?true:false} onClick={() => connectionSubmit()} className='continueBtn qsnsLastBtn'>
                                            {btnLoading?(
                                                <BtnLoading/>
                                            ):(
            
                                                "Continue"
                                            )}
                                            </Button>
                                        </div>
                                    </>
                                )
                            }

                        </div>
                    </div>
                </div>

            </div>

        </>
    )
}

export default TypeOfConnection
