import React, { useState } from 'react';
import { Avatar, Col, Row } from 'antd';
import { Box, IconButton, Paper, Typography } from '@mui/material';
import girl from '../../images/girl.png'
import PauseIcon from '@mui/icons-material/Pause';
import MicIcon from '@mui/icons-material/Mic';
import CallIcon from '@mui/icons-material/Call';
import PlayArrowIcon from '@mui/icons-material/PlayArrow';
import MicOffIcon from '@mui/icons-material/MicOff';
import { useNavigate } from 'react-router-dom';

const AudioCall = () => {
    const navigate = useNavigate()
    const [mike, setMike] = useState(false);
    const [pausePlay, setPausePlay] = useState(false);
    const handleMic = () => {
        setMike(!mike)
    }
    const leaveCall = () => {
        navigate('/chat')
    }
    const handlePausePlay = () => {
        setPausePlay(!pausePlay)
    }
    return (
        <div style={{ backgroundColor: '#000', padding: '2rem 0' }}>
            <Box
                className="call_screen_setting"
                sx={{
                    display: 'flex',
                    flexWrap: 'wrap',
                    justifyContent: 'center',
                    '& > :not(style)': {
                        m: 1,
                        width: '60%',
                    },
                }}
            >
                <Paper elevation={8} style={{ backgroundColor: '#161616', boxShadow: "0 0 15px #44dddd", margin: 0 }} >
                    <Row>
                        <Col xs={24} sm={24} md={12} lg={12} xl={12} className='w-100 d-flex mx-auto justify-center text-center' style={{ justifyContent: 'center', backgroundColor: '#fff' }}>
                            <img src={girl} style={{ maxHeight: 620, maxWidth: '100%', minHeight: 350 }} className='' alt="" />
                        </Col>
                        <Col xs={24} sm={24} md={12} lg={12} xl={12} className='' style={{ display: 'grid', placeItems: 'center', backgroundColor: '#000000', padding: '1rem 0' }}>
                            <div className="text-white" style={{ textAlign: 'center' }}>
                                {!false ?
                                    <Avatar style={{ border: '2px solid #fff', margin: 5 }} size={{ xs: 90, sm: 90, md: 90, lg: 90, xl: 110, xxl: 110 }} src={girl} />
                                    : <Avatar style={{ border: '2px solid #fff', margin: 5 }} size={{ xs: 90, sm: 90, md: 90, lg: 90, xl: 110, xxl: 110 }}>A</Avatar>}
                                <Typography variant='h4' style={{ fontWeight: 'bolder' }}>Adriane</Typography>
                                <Typography variant='body1'>Fashion Model, 24</Typography>
                                <Typography variant='body2' className='my-3' >10:52</Typography>

                            </div>
                            <div className="d-flex w-100" style={{ justifyContent: 'space-evenly' }}>
                                <IconButton onClick={() => handleMic()} style={{ backgroundColor: '#44dddd', height: 70, width: 70, borderRadius: '100%', boxShadow: "0 0 15px #00DFDF" }}>
                                    {
                                        !mike ?
                                            <MicIcon style={{ color: '#fff', fontSize: 32 }} />
                                            :
                                            <MicOffIcon style={{ color: '#fff', fontSize: 32 }} />
                                    }
                                </IconButton>
                                <IconButton onClick={() => leaveCall()} style={{ backgroundColor: '#f75a5b', height: 70, width: 70, borderRadius: '100%', boxShadow: "0 0 15px #f75a5b" }}>
                                    <CallIcon style={{ color: '#fff', fontSize: 32 }} />
                                </IconButton>
                                <IconButton onClick={() => handlePausePlay()} style={{ backgroundColor: '#44dddd', height: 70, width: 70, borderRadius: '100%', boxShadow: "0 0 15px #00DFDF" }}>
                                    {
                                        !pausePlay ?
                                            <PauseIcon style={{ color: '#fff', fontSize: 32 }} />
                                            :
                                            <PlayArrowIcon style={{ color: '#fff', fontSize: 32 }} />
                                    }
                                </IconButton>
                            </div>
                        </Col>
                    </Row>
                </Paper>
            </Box>
        </div>
    )
}
export default AudioCall