import { Button, Typography } from 'antd'
import React from 'react'
import { Container } from 'react-bootstrap'
import { useNavigate } from 'react-router-dom'

const AddUserInterest = () => {
    const navigate = useNavigate();
    const handleAddInterest = () => {
        navigate('/edit_profile')
    }
    return (
        <div className="" style={{ backgroundColor: '#000000', minHeight: '50vh' }}>
            <Container fluid className='fluid_content pt-3' style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', flexDirection: 'column' }}>
                <h2 className='text-white text-center'>Your Interests</h2>
                <Typography className='text-white text-center'>Select a few of your interests and let everyone know what you're passionate about.</Typography>
                <Typography className='text-white text-center'>- 3 choices for colors of your interests. Tap the color to make your choices.</Typography>
                <div className="home_premium mt-4" style={{ width: 'fit-content' }}>
                    <Button type='text' onClick={() => { handleAddInterest() }} style={{ color: '#fff', height: 40, width: 140 }}>Done</Button>
                </div>
            </Container>
        </div>
    )
}

export default AddUserInterest