import { Avatar, Button, Col, List, Row } from 'antd'
import React from 'react'
import { useEffect } from 'react'
import { Container } from 'react-bootstrap'
import avatar1 from '../../images/ash1.png'
import avatar2 from '../../images/ash2.png'

const Blocked = () => {
    const blockedData = [
        {
            name: 'Ansh',
            media: avatar1,
            status: 'Unblock'
        },
        {
            name: 'Max',
            media: avatar2,
            status: 'Unblock'
        },
        {
            name: 'Max',
            media: '',
            status: 'Unblock'
        },
    ]
    useEffect(() => {
        window.scrollTo({
            top: 0,
            behavior: 'smooth',
        });
    }, [])
    return (
        <div style={{ minHeight: 600 }} className="about_bg pt-4">
            <div className="d-flex mx-auto text-white py-5" style={{ justifyContent: 'center', background: '#161616' }}>
                <h2 style={{ fontWeight: 800 }}>Blocked Users</h2>
            </div>
            {blockedData?.length > 0 ?
                <Container fluid className='fluid_content pt-3 text-white' style={{ minHeight: '100vh' }}>
                    <Row gutter={[40, 40]} style={{ width: '100%', marginTop: 10 }}>
                        {blockedData?.map((data) => {
                            return (
                                <Col xs={24} sm={24} md={24} lg={12} xl={12} style={{}}>
                                    <div className="d-flex" style={{ border: '2px solid #fff', justifyContent: 'space-between', alignItems: 'center', textAlign: 'center', borderRadius: 15 }}>
                                        <div className="d-flex" style={{ alignItems: 'center' }}>
                                            {data?.media != "" ?
                                                <Avatar style={{ border: '2px solid #fff', margin: 5 }} size={{ xs: 50, sm: 50, md: 50, lg: 70, xl: 70, xxl: 70 }} src={data?.media} />
                                                : <Avatar style={{ border: '2px solid #fff', margin: 5 }} size={{ xs: 50, sm: 50, md: 50, lg: 70, xl: 70, xxl: 70 }}>A</Avatar>}
                                            <h3 style={{ marginLeft: 10 }}>{data?.name}</h3>
                                        </div>
                                        <Button className="bg-danger text-white mx-2" style={{ border: 'none', height: 45, width: 120 }}>{data?.status}</Button>
                                    </div>
                                </Col>
                            )
                        })}
                    </Row>
                </Container>
                : <div className='text-center mt-5 text-white' style={{ display: 'grid', placeItems: 'center', alignItems: 'center', textAlign: 'center' }}>
                    <h2>No Block User Found!</h2>
                </div>}
        </div>
    )
}

export default Blocked