import { apiCall } from "../../api";
import { baseUrl } from "../../constant";
import { DELETE_CONNECTION_SUCCESS, DELETE_SENT_CONNECTION_REQUEST_SUCCESS, GET_CONNECTION_LIST_SUCCESS, GET_CONNECTION_RECIVIED_REQUEST_SUCCESS, GET_CONNECTION_SENT_REQUEST_SUCCESS } from "./types";

let token = localStorage.getItem('token')
export const getSentConnectionReq = (successCb, failureCallback) => async (dispatch) => {
    let config = {
        method: "post",
        headers: { Authorization: `Bearer ${token}` },
        url: `${baseUrl}users/get_sent_requests`,
    };
    let response = await apiCall(config, dispatch);
    if (response?.data?.status) {
        successCb(response?.data);
    } else {
        failureCallback(response?.data)
    }
    dispatch({
        type: GET_CONNECTION_SENT_REQUEST_SUCCESS,
        payload: response?.data,
    });
};
