import { Button } from 'antd'
import React, { useEffect, useState } from 'react'
import { useDispatch } from 'react-redux'
import { Link, useNavigate } from 'react-router-dom'
import BtnLoading from '../../component/BtnLoading'
import five from '../../images/five.png'
import location from '../../images/location.png'
import { locationEnable } from '../../redux/actions/questions'
const LocationEnable = () => {
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const [btnLoading,setBtnLoading] = useState(false)
  

    const allowLocation = (value) => {
        setBtnLoading(true)
        const successCb = (res) => {
            console.log("succ", res)
            navigate('/notification_enable')
            setBtnLoading(false)
        }
        const failureCb = (res) => {
            setBtnLoading(false)
            console.log("failreRes", res)
        }
        const payload ={
            enable_location:JSON.stringify(value)
        }
        dispatch(locationEnable(payload, successCb, failureCb))
    }

    return (
        <>
            <div className='stepsBackground'>
                <div className='underDiv'>
                    <div className='container containeClassDiv'>
                        <div className='backAndLogo' style={{ width: "100%" }}>
                            <Link to={'/authProfile'} style={{ marginLeft: '1rem' }}><img src="plugins/images/Group 17194.png" alt='' className='backIconHeight' /></Link>
                            <img style={{ maxHeight: '200px', width: '200px' }} src='plugins/images/Mask Group 19@2x.png' />
                            <p></p>
                        </div>
                        <div className='stepss'>
                            <img src={five} alt="img_err" height="50px" className='img-fluid' />
                            <div style={{ marginTop: 40 }}>
                                <h5 style={{ color: 'white', textAlign: "center" }}>Enable Location</h5>
                                <p className='text-center text-white'>We need your permission to access <br /> your location</p>
                                <div className='mt-2 d-flex justify-content-center'>
                                    <img src={location} style={{ width: '140px', height: '140px' }} />
                                </div>
                            </div>
                            <div className='selectionGenderBox qsntLastBackImage' style={{ marginTop: 40 }}>
                                <Button disabled = {btnLoading?true:false} style={{ padding: "6px 60px" }} onClick={() => allowLocation(1)} className='continueBtn qsnsLastBtn'>
                                {
                                    btnLoading?(
                                      <BtnLoading/>
                                    ):(
            
                                     "Allow Location"
                                    )
                                  }  
                                </Button>
                            </div>
                            <Button disabled = {btnLoading?true:false} onClick={() => allowLocation(0)} type='text' className='text-center notNow mt-4 mb-4' style={{ color: '#FA81F0', fontSize: "18px" }} >
                            {
                                btnLoading?(
                                  <BtnLoading/>
                                ):(
        
                                 "Not Now"
                                )
                              } 
                            </Button>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default LocationEnable
