import { apiCall } from "../../api";
import { baseUrl } from "../../constant";
import { GET_HOME_CONNECTIONS, HOME_LEFT_SWIPE, HOME_RIGHT_SWIPE, MY_PROFILE_SUCCESS, UNDO_SWIPE_SUCCESS } from "./types";

let token = localStorage.getItem('token')
console.log("toknnnn111",token)
export const getHomeData = (successCb, failureCallback) => async (dispatch) => {
    let config = {
        method: "get",
        headers: { Authorization: `Bearer ${token}` },
        url: `${baseUrl}users/get_home_connections`,
    };
    let response = await apiCall(config, dispatch);
    if (response?.data?.status) {
        successCb(response?.data);
    } else {
        failureCallback(response?.data)
    } 
    dispatch({
        type: GET_HOME_CONNECTIONS,
        payload: response?.data,
    });
};
