import { Button, Col, Input, Row, Upload } from 'antd'
import React, { useEffect, useState } from 'react'
import moment from 'moment'
import { PlusOutlined } from '@ant-design/icons';
import { Link, useNavigate } from 'react-router-dom'
import four from '../../images/four.png'
import TextField from '@mui/material/TextField';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { FormControl, InputLabel, MenuItem, Select, Typography,InputAdornment } from '@mui/material';
import { DatePicker, Space } from 'antd';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { LocalizationProvider } from '@mui/x-date-pickers';
// import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import { Container } from 'react-bootstrap';
import { emailValidation, onlyNumberType } from '../../component/Constant';
import { genderList,getAuthProfileData,updateAuthProfile } from '../../redux/actions/questions';
import AddIcon from '@mui/icons-material/Add';
import { useDispatch, useSelector } from 'react-redux';
import { to } from '@react-spring/web';
import { toast } from 'react-toastify';
import { authProfile_bio, authProfile_dob, authProfile_email, authProfile_emailValidate, authProfile_gender, authProfile_username } from '../../toastMessages';
import BtnLoading from '../../component/BtnLoading';

const theme = createTheme({
    status: {
        danger: '#e53e3e',
    },
    palette: {
        primary: {
            main: '#FFFFFF',
            darker: '#053e85',
        },
        neutral: {
            main: '#64748B',
            contrastText: '#fff',
        },
    },
});
const AuthProfile = () => {

    const genderReduxData = useSelector((state)=>state?.questions?.interest_in_Data)
    const profileAllReduxData = useSelector((state)=>state?.questions?.get_auth_profile_data)
    console.log("genderReduxData",genderReduxData)
    console.log("profileAllReduxData",profileAllReduxData)
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const [btnLoading,setBtnLoading] = useState(false)
    const [image, setImage] = useState({ preview: "", url: "" });
    const [userName,setUserName] = useState("")
    const [phoneNumber, setPhoneNumber] = useState();
    const [userEmail, setUserEmail] = useState();
    const [gender, setGender] = useState("null");
    const [dateOfBirth, setDateOfBirth] = useState("");
    const [bio,setBio] = useState("")
    const [apiDob,setApiDob] = useState("")



    useEffect(() => {
        const successCb = (res) => {
            console.log("resSucc", res)
            // setGenderArr(res?.data)
        }
        const failureCallback = (res) => {
            console.log("resFail", res)
        }
        dispatch(genderList(successCb, failureCallback))
        dispatch(getAuthProfileData(successCb, failureCallback))
        
    }, [])

    useEffect(()=>{
        setUserName(profileAllReduxData?.username)
        setPhoneNumber(profileAllReduxData?.phone_number)
        setUserEmail(profileAllReduxData?.email)
        setGender(profileAllReduxData?.gender?._id??"null")
        setDateOfBirth()
        setBio(profileAllReduxData?.bio)
    },[profileAllReduxData])

    const ImgHandleChange = (e) => {
        if (e.target.files.length) {
            setImage({
                preview: URL.createObjectURL(e.target.files[0]),
                url: e.target.files[0]
            });
        }
    }
    console.log("image", image)

    const handleDob = (date) => {
        // console.log("dateofBRth",date)
        console.log("dateofBRth",new Date(date))
        let jsDate = new Date(date)
        // console.log("getTime",jsDate.getTime())
      
        console.log("momnt",moment().unix(jsDate))
        let momentDate =jsDate.getTime()
        setDateOfBirth(date)
        setApiDob(momentDate)
    }

    const convertToNumber = (e) => {
        let value = e.target.value
        let myPhoneNumber = onlyNumberType(value)
        console.log("myPhoneNumber",myPhoneNumber)
        if (myPhoneNumber) {
            setPhoneNumber(value)
        }
        if (phoneNumber == '') {
            setPhoneNumber(phoneNumber)
        }
    }

    const profileSubmit =()=>{
        // navigate('/locationEnable')
        let emailValidate = false
        if(userEmail){
            emailValidate = emailValidation(userEmail)
            if(!emailValidate){
                toast.error(authProfile_emailValidate)
            }
        }
        if(userName&&emailValidate&&gender!="null"&&dateOfBirth){
            console.log("emil",userEmail,"userName",userName,"phnNumber",phoneNumber,"gender",gender,"bio",bio)

            let payload={
                username:userName,
                gender:gender,
                dob:apiDob,
                bio:bio,
                email:userEmail
            }
         
                console.log('payload',payload)
                setBtnLoading(true)

                const successCb=(res)=>{
                    console.log("succres",res)
                    navigate('/locationEnable')
                    setBtnLoading(false)
                }
                const failureCb=(res)=>{
                    setBtnLoading(false)
                    console.log("failCb",res)
                }
        
                dispatch(updateAuthProfile(payload,successCb,failureCb))
            
        
        }else{
            console.log("emil",userEmail,"userName",userName,"phnNumber",phoneNumber,"gender",gender,"bio",bio)
            if(!userName){
                console.log('userName111',userName)
                toast.error(authProfile_username)
            }else if(!userEmail){
                console.log("userEmail22",userEmail)
                toast.error(authProfile_email)
            }else if(gender=="null"){
                console.log("genderrrr",gender)
                toast.error(authProfile_gender)
            }else if(!dateOfBirth){
                console.log("genderrrr",dateOfBirth)
                toast.error(authProfile_dob)
            }
            
        }
    }
    return (
        <>
            <ThemeProvider theme={theme}>
                <div className='stepsBackground'>
                    <div className='underDiv'>
                        <Container className='containeClassDiv'>
                            <div className='backAndLogo' style={{ width: "100%" }}>
                                <Link to={'/interest'} style={{ marginLeft: '1rem' }}><img src="plugins/images/Group 17194.png" alt='' className='backIconHeight' /></Link>
                                <img style={{ maxHeight: '200px', width: '200px' }} src='plugins/images/Mask Group 19@2x.png' />
                                <p></p>
                            </div>

                            <div className='stepss'>
                                <img src={four} alt="img_err" height="50px" className='img-fluid' />
                                <div style={{ marginTop: 40 }}>
                                    <h5 style={{ color: 'white', textAlign: "center", }}>Profile Details</h5>
                                </div>
                            </div>

                            <div className="profilePreview">
                                {image.preview &&
                                    <img src={image.preview} alt="dummy" className='previewImg' />
                                }
                            </div>
                            <label htmlFor="upload-button" className='plusIcon'>  <AddIcon className='addIcon' /></label>
                            <Input
                                type="file"
                                id="upload-button"
                                style={{ display: "none" }}
                                onChange={ImgHandleChange}
                            />
                            <Typography className='mt-3' style={{ color: '#00DFDF', fontSize: 18, }}>Add Profile Pic</Typography>
                            {/* </div> */}
                            <div className='ProfileMainDiv' style={{ marginTop: "55px" }}>
                                <Row gutter={[50, 40]} className="detailRow">
                                    <Col xs={24} sm={24} md={12} lg={12} xxl={12}>

                                        <TextField className='placeHoldercolor' fullWidth id="outlined-basic" label="UserName/Nickname" placeholder='Enter UserName/Nickname' variant="outlined" InputLabelProps={{ shrink: true, style: { borderColor: 'white', color: '#fff' }, }} value={userName} onChange={(e)=>setUserName(e.target.value)}/>
                                    </Col>
                                    <Col xs={24} sm={24} md={12} lg={12} xxl={12}>
                                        <TextField disabled fullWidth id="outlined-basic" label="Phone" InputProps={{maxLength: 10,startAdornment: <InputAdornment position="start">(+1) | </InputAdornment>, inputMode: 'numeric', pattern: '[0-9]*'}} placeholder='xxx-xxx-xxxx' variant="outlined" color='primary' value={phoneNumber} onChange={convertToNumber}  InputLabelProps={{shrink: true, style: { borderColor: 'white', color: '#fff' }, }} />
                                    </Col>
                                    <Col xs={24} sm={24} md={12} lg={12} xxl={12}>

                                        <TextField fullWidth id="outlined-basic" label="Email" placeholder='Enter Email' variant="outlined" color='primary' InputLabelProps={{ shrink: true, style: { borderColor: 'white', color: '#fff' }, }} value={userEmail}  onChange={(e)=>setUserEmail(e.target.value)} />
                                    </Col>
                                    <Col xs={24} sm={24} md={12} lg={12} xxl={12}>
                                        <FormControl sx={{ mb: 2 }} fullWidth className='genderSelect' >
                                            <InputLabel id="demo-simple-select">Gender</InputLabel>
                                            <Select displayEmpty id="editProfile_select" labelId="demo-simple-select" defaultValue={gender} value={gender} label="Gender" onChange={(event)=>  setGender(event.target.value)} >
                                                <MenuItem value="null" disabled selected>
                                                    <span style={{ opacity: 0.5, color: 'grey !important' }}>Select your gender</span>
                                                </MenuItem>
                                                {genderReduxData&&genderReduxData.map((item,indx)=>{
                                                    return(
                                                        <MenuItem value={item?._id}>{item?.name}</MenuItem>
                                                    )
                                                })}
                                               
                                            </Select>
                                        </FormControl>
                                    </Col>
                                    <Col xs={24} sm={24} md={24} lg={24} xxl={24} className="calender">
                                        <DatePicker suffixIcon={<img src="plugins/images/calendarIcon.png" />} value={dateOfBirth} onChange={handleDob} autoComplete="off"
                                            placeholder="Choose Birthday Date" style={{ width: "100%", border: "none", outline: "none", borderRadius: 10, height: 45, backgroundColor: '#00F1F14D' }}
                                            allowClear
                                            className="auth_profile_calender" format={"YYYY-MM-DD"}
                                        />
                                    </Col>
                                    <Col xs={24} sm={24} md={24} lg={24} xxl={24}>
                                        <TextField multiline rows={5} fullWidth InputLabelProps={{ shrink: true, style: { borderColor: 'white', color: '#fff' }, }} id="outlined-basic bio" label="Bio" variant="outlined" placeholder="Enter Bio" value={bio} onChange={(e)=>setBio(e.target.value)}/>
                                    </Col>
                                </Row>
                            </div>
                            <div className='selectionGenderBox qsntLastBackImage mb-4' style={{ marginTop: 40 }}>
                                <Button disabled = {btnLoading?true:false} onClick={profileSubmit} className='continueBtn qsnsLastBtn'>
                                {btnLoading?(
                                    <BtnLoading/>
                                ):(

                                    "Confirm"
                                )}
                                </Button>
                            </div>
                        </Container>
                    </div>
                </div>
            </ThemeProvider>
        </>
    )
}
export default AuthProfile