import React, { useState } from 'react';
import { Avatar, Col, Row } from 'antd';
import { Box, IconButton, Paper, Typography } from '@mui/material';
import girl from '../../images/girl1.png'
import PauseIcon from '@mui/icons-material/Pause';
import MicIcon from '@mui/icons-material/Mic';
import CallIcon from '@mui/icons-material/Call';
import PlayArrowIcon from '@mui/icons-material/PlayArrow';
import MicOffIcon from '@mui/icons-material/MicOff';
import VideocamIcon from '@mui/icons-material/Videocam';
import VideocamOffIcon from '@mui/icons-material/VideocamOff';
import { useNavigate } from 'react-router-dom';

const VideoCall = () => {
    const navigate = useNavigate()
    const [mike, setMike] = useState(false);
    const [playVideo, setPlayVideo] = useState(false);
    const handleMic = () => {
        setMike(!mike)
    }

    const leaveCall = () => {
        navigate('/chat')
    }

    const handleVideo = () => {
        setPlayVideo(!playVideo)
    }

    return (
        <div style={{ backgroundColor: '#000', padding: '2rem 0' }}>
            <Box
                className="call_screen_setting"
                sx={{
                    display: 'flex',
                    flexWrap: 'wrap',
                    justifyContent: 'center',
                    '& > :not(style)': {
                        m: 1,
                        width: '40%',
                    },
                }}
            >
                <Paper elevation={8} style={{ backgroundColor: '#161616', boxShadow: "0 0 15px #44dddd", margin: 0, position: 'relative' }} >
                    <img src={girl} style={{ maxHeight: 720, maxWidth: '100%', height: '100%', width: '100%' }} className='' alt="" />

                    {/* Self Video */}
                    <div className="" style={{ position: 'absolute', bottom: 150, right: 50, borderRadius: 15 }}>
                        <div className="" style={{ height: 150, boxShadow: "0 0 15px #44dddd" }}>
                            <img src={girl} style={{ height: '100%', width: '100%' }} className='' alt="" />
                        </div>
                    </div>

                    {/* Buttons */}
                    <div className="" style={{ position: 'absolute', bottom: 50, left: 0, right: 0 }}>
                        <div className="d-flex " style={{ justifyContent: 'center' }}>
                            <IconButton onClick={() => handleVideo()} style={{ backgroundColor: '#44dddd', height: 70, width: 70, borderRadius: '100%', boxShadow: "0 0 15px #00DFDF" }}>
                                {
                                    !playVideo ?
                                        <VideocamIcon style={{ color: '#fff', fontSize: 32 }} />
                                        :
                                        <VideocamOffIcon style={{ color: '#fff', fontSize: 32 }} />
                                }
                            </IconButton>
                            <IconButton onClick={() => leaveCall()} style={{ backgroundColor: '#f75a5b', height: 70, width: 70, borderRadius: '100%', boxShadow: "0 0 15px #f75a5b", margin: '0 2rem' }}>
                                <CallIcon style={{ color: '#fff', fontSize: 32 }} />
                            </IconButton>
                            <IconButton onClick={() => handleMic()} style={{ backgroundColor: '#44dddd', height: 70, width: 70, borderRadius: '100%', boxShadow: "0 0 15px #00DFDF" }}>
                                {
                                    !mike ?
                                        <MicIcon style={{ color: '#fff', fontSize: 32 }} />
                                        :
                                        <MicOffIcon style={{ color: '#fff', fontSize: 32 }} />
                                }
                            </IconButton>
                        </div>
                    </div>
                </Paper>
            </Box>
        </div>
    )
}
export default VideoCall