import { baseUrl } from "../../constant";
import { apiCall } from "../../api";
import {LOCATION_ENABLE_SUCCESS,GET_AUTH_PROFILE_DATA_SUCCESS, UPDATE_AUTH_PROFILE_SUCCESS, GET_ALL_INTEREST, GET_CONNECTION_SUCCESS, GET_INTEREST_IN_SUCCESS, UPDATE_ALL_INTEREST, UPDATE_INTEREST_IN_SUCCESS, NOTIFICATION_ENABLE_SUCCESS, UPDATE_CONNECTION_SUCCESS, UPDATE_LOCATION_ENABLE_SUCCESS } from "./types";



export const genderList = (successCb, failureCallback) => async (dispatch) => {
    let token = localStorage.getItem('token')
    let config = {
        method: "post",
        headers: { 'Content-Type': 'application/json' },
        url: `${baseUrl}interests/get_interesed_in`,
    };
    let response = await apiCall(config, dispatch);
    if (response?.data?.status) {
        successCb(response?.data);
    } else {
        failureCallback(response?.data)
    }
    dispatch({
        type: GET_INTEREST_IN_SUCCESS,
        payload: response?.data,
    });
};
