import { CardMedia } from '@mui/material'
import { Button, Col, Row } from 'antd'
import React from 'react'
import { Link, useNavigate } from 'react-router-dom'
import { signInWithPopup, GoogleAuthProvider, FacebookAuthProvider, OAuthProvider } from "firebase/auth"
import { auth } from '../../firebase/firebase'

const Sign = () => {
    const navigate = useNavigate();

    // Social Login
    const handleApplekLogin = () => {
        const appleProvider = new OAuthProvider('apple.com');
        signInWithPopup(auth, appleProvider)
            .then((result) => {
                console.log(result,'appleresult')
                // The signed-in user info.
                const user = result.user;
                console.log(user,'appleuser')
                // Apple credential
                const credential = OAuthProvider.credentialFromResult(result);
                const accessToken = credential.accessToken;
                const idToken = credential.idToken;

            }).catch((error) => {
                console.log(error, 'appleErr========')
                // Handle Errors here.
                const errorCode = error.code;
                console.log(errorCode, 'errorCode========')
                const errorMessage = error.message;
                console.log(errorMessage, 'errorMessage========')
                // The email of the user's account used.
                const email = error.customData.email;
                // The credential that was used.
                const credential = OAuthProvider.credentialFromError(error);
            })
    }

    const handleFacebookLogin = () => {
        const facebookProvider = new FacebookAuthProvider();
        signInWithPopup(auth, facebookProvider)
            .then((result) => {
                console.log(result, 'result======')
            }).catch((err) => {
                console.log(err, 'err========')
            })
    }

    const handleGoogleLogin = () => {
        const googleProvider = new GoogleAuthProvider();
        signInWithPopup(auth, googleProvider)
            .then((result) => {
                console.log(result, 'result======')
                const credential = GoogleAuthProvider.credentialFromResult(result);
                const token = credential.accessToken;
                console.log(token, 'token======')
            }).catch((err) => {
                console.log(err, 'err========')
            })
    }
    return (
        <div style={{ height: "100vh" }}>
            <Row>
                <Col xxl={12} xl={12} lg={12} className="showHide" style={{ backgroundColor: "black", height: '100vh' }}>
                    <CardMedia
                        component='video'
                        height="100%"
                        className="embed-responsive embed-responsive-16by9"
                        image="plugins/images/laf-vid.mp4"
                        alt="vid"
                        autoPlay
                    />
                </Col>
                <Col xxl={12} xl={12} lg={12} md={24} sm={24} xs={24} style={{ background: "rgb(26, 23, 23)", height: '100vh', display: 'flex', flexDirection: 'column', justifyContent: 'end' }}>
                    <div className="mb-5">
                        <div className='d-flex justify-content-center align-items-center flex-column'>
                            <img class="img-fluid" style={{ maxHeight: '200px', width: '200px' }} src='plugins/images/Mask Group 19@2x.png' alt='' />
                            <p style={{ fontSize: "20px", fontWeight: 700, marginTop: "17px", marginBottom: "10px", color: "white", fontStyle: "italic" }}>Are you ready?</p>
                            <div className='home_premium phoneLogin my-4'>
                                <Button onClick={() => navigate('/login')} style={{ height: 48, width: '100%', backgroundColor: '#161616', background: 'rgb(24, 24, 26)', color: '#fff', border: 'none' }} className='sign'>Sign In with Phone number</Button>
                            </div>
                        </div>
                        <div className='d-flex flex-column align-items-center justify-center' style={{ gap: 25 }}>
                            <Button onClick={handleApplekLogin} className='socialLogin socialWidth' style={{ backgroundColor: 'black', display: 'flex', justifyContent: 'center' }}><img className='socialLogoimg' src="plugins/images/apple.png" alt="img_err" />Sign In with Apple</Button>

                            <Button onClick={handleFacebookLogin} className='socialLogin socialWidth' style={{ backgroundColor: "#3b5998", display: 'flex', justifyContent: 'center' }}><img className='socialLogoimg' src="plugins/images/flogo.png" alt="img_err" />Sign In with Facebook</Button>
                            
                            <Button onClick={handleGoogleLogin} className='socialLogin socialWidth' style={{ color: "black", display: 'flex', justifyContent: 'center' }}><img className='socialLogoimg' src="plugins/images/google.png" alt="img_err" />Sign In with Google</Button>
                        </div>
                    </div>
                    <div className='mt-5'>
                        <p style={{ textAlign: 'center', fontSize: '12px', fontWeight: '100', color: 'white', marginBottom: '0' }}>By creating an account, you agree to our</p>
                        <p className='termPTag'><span className='termSpanUnderline' onClick={() => navigate('/authTerms')}>Terms of Service </span><span style={{ fontWeight: '100' }}>and</span> <span className='termSpanUnderline' onClick={() => navigate('/authPrivacy')}>
                            Privacy Policy
                        </span></p>
                    </div>
                </Col>
            </Row>
        </div>
    )
}
export default Sign