import { Box, Card, CardMedia, TextField, Typography } from '@mui/material'
import { Button, Input } from 'antd'
import React from 'react'
import { useState } from 'react'
import { Container } from 'react-bootstrap'
import { useNavigate } from 'react-router-dom';
import SendIcon from '@mui/icons-material/Send';

const MatchScreen = () => {
    const navigate = useNavigate();
    const [message, setMessage] = useState('')

    const handleMessage = (e) => {
        setMessage(e)
        console.log("e", e)
    }

    const sendMessage = () => {
        console.log("Done")
        setMessage('')
    }
    // Hit enter to send data...
    const checkEnterClicked = (inputObj) => {
        if (inputObj.keyCode === 13) {
            sendMessage();
        }
    }

    return (
        <div className='text-center' style={{ backgroundColor: '#000', color: '#fff' }}>
            <Container fluid className='fluid_content py-3'>
                <Card variant="outlined" style={{ minHeight: '75vh', backgroundColor: '#fff', width: '100%', position: 'relative', display: 'flex', justifyContent: 'center', flexDirection: 'column', alignItems: 'center', borderRadius: 20 }}>
                    <h1 className='mt-2'>Congratulations</h1>
                    <Typography variant='body1'>You have a connection!</Typography>
                    <div className="d-flex match_card_style" style={{ justifyContent: 'center', backgroundImage: 'url("' + "plugins/images/serca.png" + '")', backgroundPosition: "center", backgroundSize: "contain", backgroundRepeat: 'no-repeat', borderRadius: '10px 10px 0 0', width: '100%', alignItems: 'center' }}>
                        <CardMedia
                            component='img'
                            style={{ margin: 0, transform: `rotate(-10deg)`, borderRadius: 20, boxShadow: 'rgba(0, 0, 0, 0.24) 0px 3px 8px' }}
                            className="match_both_img"
                            image="plugins/images/avtr8.png"
                            alt="vid"
                            controls
                        />
                        <CardMedia
                            component='img'
                            style={{ margin: 0, transform: `rotate(10deg)`, borderRadius: 20, boxShadow: 'rgba(0, 0, 0, 0.24) 0px 3px 8px' }}
                            className="match_both_img"
                            image="plugins/images/avtr3.png"
                            alt="vid"
                            controls
                        />
                    </div>
                    <div className="d-flex mb-3" style={{ justifyContent: 'space-evenly', flexWrap: 'wrap', width: '60%', gap: 20 }} >
                        <div className='' style={{ width: 'auto', minWidth: '40%' }}>
                            <Input
                                placeholder="Say Hello"
                                className='match_input'
                                value={message}
                                onKeyDown={(e) => checkEnterClicked(e)} onChange={(e) => handleMessage(e.target.value)}
                                style={{ margin: 0, padding: 0, height: 52, background: 'transparent', borderRadius: 15, cursor: 'pointer', border: '2px solid #00DFDF', width: '100%' }}
                                suffix={<SendIcon onClick={() => { navigate('/chat'); sendMessage() }} fontSize="large" style={{ color: '#00DFDF', marginRight: 10 }} />}
                            />
                        </div>
                        <Button block onClick={() => navigate('/home')} type='text' className='common_button mt-3 mb-4 mt-sm-0 mb-sm-5' style={{ width: 'auto', backgroundColor: '#00DFDF', boxShadow: "0 0 15px #00DFDF", minWidth: '40%' }}>Continue Browsing</Button>
                    </div>
                </Card>
            </Container>
        </div>
    )
}

export default MatchScreen