import React, { useState } from 'react'
import { Button, Col, Modal, Row, } from 'antd'
import { Link, NavLink, useNavigate } from 'react-router-dom'
import { Navbar, Container } from 'react-bootstrap'
import { BiLogOutCircle } from 'react-icons/bi';
import { MenuOutlined } from '@ant-design/icons';
import Drawer from 'react-modern-drawer'
import 'react-modern-drawer/dist/index.css'
import { Input } from 'antd';
import { Checkbox, Divider, FormControl, FormControlLabel, FormLabel, Radio, RadioGroup, Slider, Typography } from '@mui/material';
import CancelIcon from '@mui/icons-material/Cancel';
import mdUnCheck from '../images/md-checkbox.png'
import mdCheck from '../images/md-checked.png'
const { Search } = Input;

const Header = () => {
    const navigate = useNavigate();
    const [isDrawerOpen, setIsDrawerOpen] = useState(false)
    const [openFilter, setOpenFilter] = useState(false)
    let activeStyle = { color: '#000' };
    const [checked, setChecked] = useState(true);
    const [value, setValue] = useState([20, 37]);
    const [distanceValue, setDistanceValue] = useState('4');
    const [global, setGlobal] = useState(false);
    const handleChange = (event) => {
        setChecked(event.target.checked);
    };

    const toggleDrawer = () => {
        setIsDrawerOpen((prevState) => !prevState)
    }

    const handleFilter = () => {
        setOpenFilter(true)
    }
    const handleChangeGlobal = (e) => {
        setGlobal(e.target.checked)
    };

    const handleAgeChange = (event, newValue) => {
        setValue(newValue);
    }
    const handleDistanceChange = (event, newValue) => {
        setDistanceValue(event.target.value)
    }

    const label = { inputProps: { 'aria-label': 'Checkbox demo' } };
    return (
        <div style={{ backgroundColor: '#000000', height: 110, display: 'flex', alignItems: 'center' }}>
            <Container fluid className='fluid_content'>
                <Navbar className='nav_back' expand="lg">
                    <Container fluid className="navbar_container_width">
                        <div className="d-flex justify-content-between align-items-center w-100">
                            <Link to="/home" className="nav_logo"><img src='plugins/images/header_logo.png' className='nav_logo_style' style={{}} alt="navLogo" /></Link>
                            <div className="d-none d-lg-block">
                                <NavLink to="/home" style={({ isActive }) => isActive ? activeStyle : undefined} className="Header_text_style">Home</NavLink>
                                <NavLink to="/discover" style={({ isActive }) => isActive ? activeStyle : undefined} className="Header_text_style">Discover</NavLink>
                                <NavLink to="/map" style={({ isActive }) => isActive ? activeStyle : undefined} className="Header_text_style">Map</NavLink>
                                <NavLink to="/connection" style={({ isActive }) => isActive ? activeStyle : undefined} className="Header_text_style">Connection</NavLink>
                                <NavLink to="/chat" style={({ isActive }) => isActive ? activeStyle : undefined} className="Header_text_style">Chat</NavLink>
                                <NavLink to="/profile" style={({ isActive }) => isActive ? activeStyle : undefined} className="Header_text_style">Profile</NavLink>
                            </div>
                            <div className='d-none d-lg-block'>
                                <div style={{ display: 'flex', justifyContent: 'space-betwen', alignItems: 'center' }}>
                                    <Input
                                        placeholder="Search..."
                                        readOnly
                                        className='header_search_bg_color'
                                        onClick={() => handleFilter()}
                                        style={{ margin: 0, padding: "0 0 0 10px", height: 35, background: '#000', borderRadius: 10, cursor: 'pointer', borderColor: '#fff' }}
                                        suffix={<img onClick={() => handleFilter()} src="plugins/images/filter.png" style={{ height: 35 }} />}
                                    />
                                    <div className=""></div>
                                    <img onClick={() => { navigate('/setting') }} src="plugins/images/setting.png" style={{ cursor: 'pointer', margin: '0 15px', height: 35 }} />
                                </div>
                            </div>
                            <MenuOutlined onClick={toggleDrawer} style={{ fontSize: 26 }} className='d-lg-none text-white' />
                        </div>
                        <Drawer open={isDrawerOpen} onClose={toggleDrawer} direction='top' style={{ height: 'auto', background: "linear-gradient(to right, #00DFDF, #F882EF)" }} className='drawer_style' overlayColor="#000" enableOverlay >
                            <div style={{ display: 'flex', justifyContent: 'center', flexDirection: 'column', alignItems: 'center' }}>
                                <div onClick={() => { setIsDrawerOpen((prevState) => !prevState); navigate('/home') }} className="nav_logo my-3">
                                    Lonely AF
                                </div>
                                <div style={{ display: 'flex', justifyContent: 'center', flexDirection: 'column' }}>
                                    <Link to="/home" onClick={() => setIsDrawerOpen((prevState) => !prevState)} className="drawer_text_style">Home</Link>
                                    <Link to="/discover" onClick={() => setIsDrawerOpen((prevState) => !prevState)} className="drawer_text_style">Discover</Link>
                                    <Link to="/map" onClick={() => setIsDrawerOpen((prevState) => !prevState)} className="drawer_text_style">Map</Link>
                                    <Link to="/connection" onClick={() => setIsDrawerOpen((prevState) => !prevState)} className="drawer_text_style">Connection</Link>
                                    <Link to="/chat" onClick={() => setIsDrawerOpen((prevState) => !prevState)} className="drawer_text_style">Chat</Link>
                                    <Link to="/profile" onClick={() => setIsDrawerOpen((prevState) => !prevState)} className="drawer_text_style">Profile</Link>
                                    <Link onClick={() => { setIsDrawerOpen((prevState) => !prevState); handleFilter() }} className="drawer_text_style">Filter</Link>
                                    <Link to="/setting" onClick={() => setIsDrawerOpen((prevState) => !prevState)} className="drawer_text_style">Setting</Link>
                                    <Button onClick={() => { navigate('/'); localStorage.clear(); setIsDrawerOpen((prevState) => !prevState) }} style={{ color: 'red', width: 150, justifyContent: 'space-evenly' }} className="drawer_text_style">Logout <BiLogOutCircle /></Button>
                                </div>
                            </div>
                        </Drawer>
                    </Container>
                </Navbar>
            </Container>

            {/* Filter */}
            <Modal centered open={openFilter} footer={null} onOk={() => setOpenFilter(false)} onCancel={() => setOpenFilter(false)}
                width={900}
                bodyStyle={{ display: 'flex', justifyContent: 'center', flexDirection: 'column', }}
            >
                <div className="">
                    <div className="d-flex" style={{ justifyContent: 'space-between', width: '80%', margin: 'auto', alignItems: 'center' }}>
                        <CancelIcon onClick={() => setOpenFilter(false)} style={{ color: '#fff', cursor: 'pointer' }} />
                        <Typography variant='h5' style={{ fontWeight: '800' }}>Search Filters</Typography>
                        <Typography variant='body1' style={{ textDecoration: 'underline', color: '#00DFDF', cursor: 'pointer' }}>Clear</Typography>
                    </div>
                    {/* Intrested */}
                    <div className="">
                        <FormControl
                            component="fieldset"
                            sx={{ m: 3, width: '100%' }}
                            variant="standard"
                        >
                            <FormLabel component="legend" style={{ color: '#FA81F0' }}>Intrested In</FormLabel>
                            <Row>
                                <Col md={6} lg={6} xl={6} xxl={6}>
                                    <FormControlLabel
                                        control={<Checkbox icon={<img src={mdUnCheck} />} checkedIcon={<img src={mdCheck} />} onChange={handleChange} />}
                                        label="Male"
                                    />
                                </Col>
                                <Col md={6} lg={6} xl={6} xxl={6}>
                                    <FormControlLabel
                                        control={<Checkbox icon={<img src={mdUnCheck} />} checkedIcon={<img src={mdCheck} />} onChange={handleChange} />}
                                        label="Female"
                                    />
                                </Col>
                                <Col md={6} lg={6} xl={6} xxl={6}>
                                    <FormControlLabel
                                        control={<Checkbox icon={<img src={mdUnCheck} />} checkedIcon={<img src={mdCheck} />} onChange={handleChange} />}
                                        label="Couples"
                                    />
                                </Col>
                                <Col md={6} lg={6} xl={6} xxl={6}>
                                    <FormControlLabel
                                        control={<Checkbox icon={<img src={mdUnCheck} />} checkedIcon={<img src={mdCheck} />} onChange={handleChange} />}
                                        label="Others"
                                    />
                                </Col>
                            </Row>
                        </FormControl>
                    </div>
                    <Divider style={{ backgroundColor: '#fff' }} />
                    {/* Connection */}
                    <div className="">
                        <FormControl component="fieldset" sx={{ m: 3, width: '100%' }} variant="standard" >
                            <div className="d-flex" style={{ justifyContent: 'space-between' }}>
                                <FormLabel component="legend" style={{ color: '#FA81F0' }}>Connection Type</FormLabel>
                                <FormControl style={{ minWidth: '15%' }}>
                                    <RadioGroup aria-labelledby="demo-radio-buttons-group-label" name="radio-buttons-group" >
                                        <FormControlLabel value="" control={<Radio style={{ color: '#fff' }} />} label="Select All" />
                                    </RadioGroup>
                                </FormControl>
                            </div>
                            <Row>
                                <Col md={6} lg={6} xl={6} xxl={6}>
                                    <FormControlLabel
                                        control={<Checkbox icon={<img src={mdUnCheck} />} checkedIcon={<img src={mdCheck} />} onChange={handleChange} />}
                                        label="Local Hookup"
                                    />
                                </Col>
                                <Col md={6} lg={6} xl={6} xxl={6}>
                                    <FormControlLabel
                                        control={<Checkbox icon={<img src={mdUnCheck} />} checkedIcon={<img src={mdCheck} />} onChange={handleChange} />}
                                        label="Dating"
                                    />
                                </Col>
                                <Col md={6} lg={6} xl={6} xxl={6}>
                                    <FormControlLabel
                                        control={<Checkbox icon={<img src={mdUnCheck} />} checkedIcon={<img src={mdCheck} />} onChange={handleChange} />}
                                        label="Cuddle"
                                    />
                                </Col>
                            </Row>
                        </FormControl>
                    </div>
                    <Divider style={{ backgroundColor: '#fff' }} />

                    {/* Distance */}
                    <div className="m-3">
                        {
                            !global ?
                                <div className="">
                                    <div className="d-flex mt-3" style={{ justifyContent: 'space-between' }}>
                                        <Typography style={{ color: '#FA81F0' }}>Distance</Typography>
                                        <Typography>{distanceValue} miles</Typography>
                                    </div>
                                    <Slider style={{ color: '#00DFDF' }} min={1} max={100} value={distanceValue} onChange={(e) => handleDistanceChange(e)} aria-label="Default" valueLabelDisplay="auto" />
                                </div>
                                : null
                        }
                        <FormControl
                            component="fieldset"
                            sx={{ m: 1 }}
                            variant="standard"
                        >
                            <FormControlLabel
                                control={<Checkbox icon={<img src={mdUnCheck} />} checkedIcon={<img src={mdCheck} />} onChange={(e) => handleChangeGlobal(e)} />}
                                label="Global"
                            />
                        </FormControl>
                    </div>
                    <Divider style={{ backgroundColor: '#fff' }} />

                    {/* Age */}
                    <div className="m-3">
                        <div className="">
                            <div className="d-flex mt-3" style={{ justifyContent: 'space-between' }}>
                                <Typography style={{ color: '#FA81F0' }}>Age</Typography>
                                <Typography>{value[0]} - {value[1]}</Typography>
                            </div>
                            <Slider style={{ color: '#00DFDF' }} min={18} max={100} getAriaLabel={() => 'Age range'} value={value} onChange={handleAgeChange} valueLabelDisplay="auto" />
                        </div>
                    </div>
                    <Divider style={{ backgroundColor: '#fff' }} />

                    {/* Account Verification */}
                    <div className="">
                        <FormControl
                            component="fieldset"
                            sx={{ m: 3, width: '100%' }}
                            variant="standard"
                        >
                            <FormLabel component="legend" style={{ color: '#FA81F0' }}>Account Verification</FormLabel>
                            <Row>
                                <Col md={8} lg={8} xl={8} xxl={8}>

                                    <FormControlLabel
                                        control={<Checkbox icon={<img src={mdUnCheck} />} checkedIcon={<img src={mdCheck} />} onChange={handleChange} />}
                                        label="Verified Photo"
                                    />
                                </Col>
                                <Col md={8} lg={8} xl={8} xxl={8}>

                                    <FormControlLabel
                                        control={<Checkbox icon={<img src={mdUnCheck} />} checkedIcon={<img src={mdCheck} />} onChange={handleChange} />}
                                        label="Driving License"
                                    />
                                </Col>
                                <Col md={8} lg={8} xl={8} xxl={8}>

                                    <FormControlLabel
                                        control={<Checkbox icon={<img src={mdUnCheck} />} checkedIcon={<img src={mdCheck} />} onChange={handleChange} />}
                                        label="Background Check"
                                    />
                                </Col>
                            </Row>
                        </FormControl>
                    </div>
                </div>
                <div className="home_premium" style={{ width: '50%', margin: 'auto' }}>
                    <Button className='common_button' style={{ background: 'transparent', width: '100%' }}>Save</Button>
                </div>
            </Modal>
        </div>
    )
}
export default Header